* {
    margin: 0;
}

html, body {
    height: 100%;
}

a {
    text-decoration:none;
    color: inherit;
}

#root {
    height:100%;
}

.Screen {
    margin: 0 auto;
    padding-top:10px;
    max-width: 560px;
    height: 900px;
    border: 1px solid black;
    background-color: #DAFFF2;
    display:grid;
    grid-template-rows: 6fr 1fr;
}

.MenuBar {
    background-color: #FFDAB9;
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
    box-shadow: 0px 1px 10px 0px #000000;
}

.MenuBarItem {
    display:inline-block;
    margin: auto;
    font-size: 50px;
}

.MenuBarItem:link, .MenuBarItem:visited, .MenuBarItem:active {
    color: inherit;
}

.MenuBarItem:hover {
    color: #AAAAAA;
}

.App {
    text-align: center;
    background-color: #333333;
    height:100%;
    padding-top:20px;
    font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
}

.FaultReport, .Wall, .Booking {
    height:100%;
    padding: 10px;
    padding-top: 30px;
}

.FaultReport {
    display: grid;
    grid-template-rows: 1fr 1fr 5fr;
    grid-template-columns: 1fr;
}

.Wall h1, .Booking h1 {
    padding-bottom:20px;
}

.FaultReportButton {
    width: 30%;
    height: 50px;
    margin:10px;
}

.FaultReportText textarea {
    font-size:15px;
}

.FaultReportHeader {
    font-size: 30px;
}

.FaultReportChoicesItem {
    font-size: 44px;
    padding: 30px;
}

.FaultReportChoicesItem div:hover {
    color: #AAAAAA;
}

.FaultReportChoicesItem {
    font-size: 40px;
}

.FaultReportChoicesItem:active {
    color: #aaaaaa;
    -webkit-animation: fadeinout 4s linear forwards;
    animation: fadeinout 4s linear forwards;
}

@-webkit-keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}

@keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}

.Post {
    border:1px solid black;    
    width:80%;
    margin:auto;
    text-align: left;
    padding:10px;
    margin-bottom: 10px;
}

.PostHeader {
    font-size: 24px;
    margin-bottom: 10px;
}

.Bookable {
    border:1px solid black;    
    width:80%;
    margin:auto;
    text-align: left;
    padding:10px;
    margin-bottom: 20px;
}

.BookableHeader {
    font-size: 28px;
    padding:14px;
}

.BookableSelectedSchedule {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
}

.BookableScheduleMonth {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    width: 80%;
    margin:auto;
}

.BookableScheduleDay {
    font-size:20px;
    display:inline-block;
    border: 1px solid black;
    margin-left: -1px;
    margin-bottom: -1px;
    padding:14px;
}

.BookableSelectedHeader {
    margin-bottom: 20px;
    padding:14px;
    padding-top:30px;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.Profile {
    padding: 30px;
}

.ProfileName {
    font-size: 30px;
}

.ProfilePicture {
    font-size: 200px;
}

.ProfileData {
    width:80%;
    margin:auto;
    font-size: 20px;
    text-align:left;
}

.ProfileDataUsers {
    border:1px solid black;
    padding: 10px;
    height: 46px;
}

.ProfileDataUsers i {
    font-size: 70%;
}

